import * as React from 'react';
import { DataGrid } from '@material-ui/data-grid';
import { Card } from '@material-ui/core';
import columns from './columns';
import axios from 'axios';
import API from '../../Environment/config';

import { ClienteContext } from 'src/context/ClienteContext';
// import PerfectScrollbar from 'react-perfect-scrollbar';
import alertify from 'alertifyjs';

const END_POINT = {
  actualizarCliente: 'api/clientes'
};

export default function TablaClientes({}) {
  const [tableIsLoading, setTableIsLoading] = React.useState(false);
  const {
    clientes,
    clientesFiltro,
    setClientes,
    setDeleteCliente,
    cargarClientes
  } = React.useContext(ClienteContext);

  React.useEffect(() => {
    cargarClientes();
  }, []);

  const editarCliente = (cliente) => {
    if (cliente.id) {
      var field = cliente.field;
      var clienteNuevo = [];

      const nuevoListado = clientes.map((item) => {
        if (item.id === cliente.id) {
          if (field === 'correo')
            clienteNuevo = {
              ...item,
              correo: cliente.props.value
            };
          if (field === 'cedula')
            clienteNuevo = {
              ...item,
              cedula: cliente.props.value
            };
          if (field === 'nombres')
            clienteNuevo = {
              ...item,
              nombres: cliente.props.value
            };
          if (field === 'telefono')
            clienteNuevo = {
              ...item,
              telefono: cliente.props.value
            };
          if (field === 'direccion')
            clienteNuevo = {
              ...item,
              direccion: cliente.props.value
            };
          if (field === 'observacion')
            clienteNuevo = {
              ...item,
              observacion: cliente.props.value
            };

          updateclientesDB(clienteNuevo);

          return clienteNuevo;
        }

        return item;
      });

      setClientes(nuevoListado);
    }
  };

  const updateclientesDB = async (cliente) => {
    setTableIsLoading(true);

    const result = await API.patch(
      END_POINT.actualizarCliente + '/' + cliente.id,
      cliente
    );

    setTableIsLoading(false);
    alertify.success('Actualizado', 2);
    console.log(result);
  };

  const onRowSelectEvent = (parameters) => {
    setDeleteCliente(parameters.data);
    // alert(JSON.stringify(parameters.data));
  };
  return (
    <Card>
      <div style={{ height: 360, width: '100%' }}>
        <DataGrid
          rows={clientesFiltro}
          columns={columns}
          datasets="Commodity"
          checkboxSelection={false}
          onEditCellChangeCommitted={editarCliente}
          pageSize={10}
          disableSelectionOnClick={false}
          rowHeight={23}
          onRowSelected={onRowSelectEvent}
          loading={tableIsLoading}
        />
      </div>
    </Card>
  );
}
