import React from 'react';
import Tooltip from '@material-ui/core/Tooltip';
export default [
  {
    field: 'id',
    headerName: 'ID',
    width: 90,
    visible: false
  },
  {
    field: 'cedula',
    headerName: 'Cdula',
    width: 130,
    editable: true
  },
  {
    field: 'nombres',
    headerName: 'Nombres',
    width: 300,
    editable: true,
    renderCell: (cellValues) => {
      return (
        <Tooltip title={cellValues.formattedValue}>
          <span className="table-cell-trucate">
            {cellValues.formattedValue}
          </span>
        </Tooltip>
      );
    }
  },
  {
    field: 'correo',
    headerName: 'Correo',
    width: 250,
    editable: true
  },
  {
    field: 'telefono',
    headerName: 'Telfono',
    width: 250,
    editable: true
  },
  {
    field: 'direccion',
    headerName: 'Direccin',
    // type: 'number',
    width: 400,
    editable: true
  },
  {
    field: 'observacion',
    headerName: 'Observacin',
    // type: 'number',
    width: 350,
    editable: true
  }
];
