import React, { useContext, useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import axios from 'axios';
import alertify from 'alertifyjs';
import { ClienteContext } from '../../context/ClienteContext';
import { EstadisticasContext } from '../../context/EstadisticasContext';

import API from '../../Environment/config';

import {
  Box,
  Button,
  Card,
  CardContent,
  TextField,
  InputAdornment,
  SvgIcon,
  makeStyles
} from '@material-ui/core';
import { Search as SearchIcon } from 'react-feather';
import Input from '@material-ui/core/Input';
import CircularProgress from '@material-ui/core/CircularProgress';
const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1)
    }
  },
  importButton: {
    marginRight: theme.spacing(1)
  },
  exportButton: {
    marginRight: theme.spacing(1)
  }
}));

const END_POINT = {
  nuevoCliente: 'api/clientes'
};

const NuevoCliente = ({ className, ...rest }) => {
  const classes = useStyles();
  const { setIsNewClient, setClientes, clientes, setClientesFiltro } =
    useContext(ClienteContext);
  const { setIsReload } = useContext(EstadisticasContext);

  const [cedula, setCedula] = useState('');
  const [nombres, setNombres] = useState('');
  const [telefono, setTelefono] = useState('');
  const [direccion, setDireccion] = useState('');
  const [correo, setCorreo] = useState('');
  const [observacion, setObservacion] = useState('');

  const [isLoading, setIsLoading] = useState(false);
  const guardarCliente = async () => {
    if (cedula === '') {
      alertify.error('La cdula es obligatoria', 2);
      return;
    }
    if (nombres === '') {
      alertify.error('Los nombres son obligatorios', 2);
      return;
    }

    let telef = telefono,
      direc = direccion,
      corr = correo,
      obs = observacion;

    if (telef === '') telef = '-';
    if (direc === '') direc = '-';
    if (corr === '') corr = '-';
    if (obs === '') obs = '-';

    setIsLoading(true);
    const nuevoCliente = {
      cedula,
      nombres,
      telefono: telef,
      direccion: direc,
      correo: corr,
      observacion: obs
    };

    const respuesta = await API.post(END_POINT.nuevoCliente, nuevoCliente);
    setIsLoading(false);

    if (respuesta.data.estado !== 201) {
      alertify.error(respuesta.data.mensaje, 2);
      return;
    }

    setIsReload(true);
    setCedula('');
    setNombres('');
    setTelefono('');
    setDireccion('');
    setCorreo('');
    setObservacion('');
    setClientes([respuesta.data.cliente, ...clientes]);
    setClientesFiltro([respuesta.data.cliente, ...clientes]);
    setIsNewClient(false);
  };

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <Box mt={3}>
        <Card>
          <CardContent className={classes.root}>
            <h2> Nuevo cliente</h2>
            <Input
              defaultValue={cedula}
              onChange={(e) => setCedula(e.target.value)}
              placeholder="Cdula"
              inputProps={{ 'aria-label': 'Cdula' }}
            />
            <Input
              defaultValue={nombres}
              onChange={(e) => setNombres(e.target.value)}
              placeholder="Nombres"
              inputProps={{ 'aria-label': 'Nombres' }}
            />
            <Input
              defaultValue={telefono}
              onChange={(e) => setTelefono(e.target.value)}
              placeholder="Telfono"
              inputProps={{ 'aria-label': 'Telfono' }}
            />
            <br />
            <Input
              defaultValue={direccion}
              onChange={(e) => setDireccion(e.target.value)}
              placeholder="Direccin"
              inputProps={{ 'aria-label': 'Direccin' }}
            />
            <Input
              defaultValue={correo}
              onChange={(e) => setCorreo(e.target.value)}
              placeholder="Correo"
              inputProps={{ 'aria-label': 'Correo' }}
            />

            <Input
              defaultValue={correo}
              onChange={(e) => setObservacion(e.target.value)}
              placeholder="Observacin"
              inputProps={{ 'aria-label': 'Observacin' }}
            />
            {/* <Box maxWidth={500}>
              <TextField
                fullWidth
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <SvgIcon
                        fontSize="small"
                        color="action"
                      >
                        <SearchIcon />
                      </SvgIcon>
                    </InputAdornment>
                  )
                }}
                placeholder="Search customer"
                variant="outlined"
              />
            </Box> */}
          </CardContent>
          <Box display="flex" justifyContent="flex-end">
            {isLoading ? <CircularProgress /> : null}

            <Button
              style={{ backgroundColor: 'rgb(220, 0, 78)', color: 'white' }}
              variant="contained"
              onClick={() => setIsNewClient(false)}
            >
              Cancelar
            </Button>
            <Button
              color="primary"
              variant="contained"
              onClick={guardarCliente}
            >
              Guardar Cliente
            </Button>
          </Box>
        </Card>
      </Box>
    </div>
  );
};

export default NuevoCliente;
